@import url('https://fonts.googleapis.com/css2?family=Concert+One&display=swap');

*{
    margin: 0;
    box-sizing: border-box;
}

body{
    background-color: #363745;
}

.container{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
}

.title{
    font-family: 'Concert One', serif;
    font-size: 40px;    
    color: whitesmoke    
}

.quiz-container {
    border: 2px solid rgb(28, 27, 32);
    border-radius: 5px;
}

.answer{
    text-align: left;
    display: flex;
    align-items: center;
    margin-bottom: 10px;    
    color: whitesmoke
}


